import { get } from '../utils/APIService';

const Index = ({todos}) => {
  return (
    <div>
      {todos.map(todo => (
        <div key={todo.id}>{todo.title}</div>
      ))}
    </div>
  )
}

Index.getInitialProps = async function () {
  const response = await APIService.get('/todos');

  return {
    todos: response.data,
  };
};

export default Index;
